import React, { Component } from 'react';
import { Route, Link } from "react-router-dom";
import Home from "./Home";
import Subpage from "./Subpage";
import Design from "./Design";
import NotFoundComponent from "./NotFound";
import $ from 'jquery';
import TransitionGroup from "react-transition-group/TransitionGroup";

const firstChild = props => {
  const childrenArray = React.Children.toArray(props.children);
  return childrenArray[0] || null;
};

function logFunction() {
    $('.card .text-reveal > span:nth-child(2) > span').css({"-webkit-transform":"translateX(0)"})
    $('.card .text-reveal > span:nth-child(2) > span > span').css({"-webkit-transform":"translateX(0)"})
}

class App extends Component {

  render() {
    return (
      <div className="App">
      <div id="#covergg"></div>

        <div className="TopBar">
          <Link to="/" id="home_link">Home</Link>
          <Link to="/code" id="code_link">Code</Link>
          <Link to="/design" id="design_link">Design</Link>
        </div>
        <Route exact path="/" onChange={logFunction()}
            render={({ match, ...rest }) => (
          <TransitionGroup component={firstChild}>
            {match && <Home {...rest} />}
          </TransitionGroup>
        )}/>
        <Route
         exact path="/code"
         render={({ match, ...rest }) => (
           <TransitionGroup component={firstChild}>
             {match && <Subpage {...rest} />}
           </TransitionGroup>
        )}/>
        <Route
         exact path="/design"
         render={({ match, ...rest }) => (
           <TransitionGroup component={firstChild}>
             {match && <Design {...rest} />}
           </TransitionGroup>
        )}/>
        <Route
         path="/*"
         render={({ match, ...rest }) => (
           <TransitionGroup component={firstChild}>
             {match && <NotFoundComponent {...rest} />}
           </TransitionGroup>
        )}/>
      </div>
    );
  }

}
export default App;



